body,* {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
.nav-bar {
  box-sizing: border-box;
  padding-inline: 224px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  z-index: 5;
  height: 94px;
  width: 100%;
  position: fixed;
  background: #FFFFFF;
  box-shadow: 0px 4px 4px rgba(42, 45, 123, 0.02);
}
.main-section{
  background: #FFFCEF;
  padding-top: 94px;
  min-height: 100vh;
  padding-inline: 150px;
}
.logo{
  height: 62px;
  width: 60px;
}
a{
  text-decoration: none;
}
.links{
  list-style: none;
  display: inline-block;
}
.links li{
  display: inline-block;
  padding: 30px;
  font-size: 16px;
  line-height: 19.2px;
  font-weight: 400;

}
.links li:last-child{
  padding-right: 0px;
}
.main-header{
  margin-top: 40px;
  margin-bottom: 40px;
  font-size: 24px;
  line-height: 34.54px;
  font-weight: 600;
  color: #606060;
}
.drop-down{
  box-sizing: border-box;
  display: inline-block;
  padding-left: 24px;
  padding-right: 16.52px;
  padding-top: 13px;
  padding-bottom: 12px;
  border: 1px solid #b8b298 ;
  border-radius: 9px;
  position: relative;
  background: #fff;
  margin-right: 24px;
}
.drop-down span{
  padding-right: 10.2px;
  font-size:16px;
  line-height: 19.2px;
  color: #606060;
}
.drop-down i{
  padding-right: 16.52px;
  color: #606060;
}

.drop-down-list{
  display: none;
  z-index: 2;
  padding-top: 30px;
  width: 345px;
  background: #FFFFFF;
  border: 1px solid #F2EEDA;
  box-sizing: border-box;
  box-shadow: 0px 2px 20px rgba(96, 96, 96, 0.4);
  border-radius: 8px;
  position: absolute;
  left: 0;
  top: calc(100% + 10px);
  transition: .2s;
}
.career{
  padding: 0;
}
.career li input{
  height: 16px;
  width: 16px;
  margin-left: 16px;
  margin-right: 8px;
  margin-top: 0;
  margin-bottom: 0;
}
.skills li input{
  margin-left: 5px;
}
.drop-down-list li label{
  font-size: 14px;
  display: inline-block;
}
.drop-down-list ul li{
  list-style: none;
  display: flex;
  align-items: center;
}
.career ul{
  margin-bottom: 24px;
}
.career li{
  margin-bottom: 16px;
}
.drop-down-list hr{
  margin-top: 41px;
  margin-bottom: 0;
  height: 0;
  border: 1px solid #F2EEDA;
}
.drop-down-list .bottom{
  display: flex;
  padding-block: 16px;
  padding-inline:24px ;
  justify-content: space-between;
  align-items: baseline;
}
.drop-down-list .bottom span{
  display: inline-block;
}
.drop-down-list .bottom span:first-child{

  font-size: 14px;
}
.drop-down-list .bottom span:last-child{
  box-sizing: border-box;
  padding-inline: 40px;
  padding-block: 12px;
  height: 42px;
  background: linear-gradient(90deg, #7A62F8 0%, #630BFF 100%);
  border-radius: 8px;
  color: #fff;
}
.card-section{
  display: grid;
  grid-template-columns: repeat(auto-fill,minmax(500px,1fr));
  row-gap: 32px;
  column-gap: 24px;
  margin-top: 40px;
  padding-bottom: 158px;
}
.card{
  width: 500px;
  height: 339px;
  background: #FFFFFF;
  border: 1px solid #F2EEDA;
  box-sizing: border-box;
  border-radius: 16px;
  padding: 24px;
}
.card .top{
  display: flex;

}
.card .top .content{
  margin-left: 16px;
}
.card .top h2{
  margin-bottom: 16px;
  font-style: normal;
font-weight: 700;
font-size: 24px;
line-height: 29px;
color: #282828;
}
.card .top h3{

  font-style: normal;
font-weight: 400;
font-size: 16px;
line-height: 19px;

color: #606060;
}
.card hr{
  margin-top: 16px;
  margin-bottom: 16px;
  height: 0px;
margin-left: -24px;
margin-right: -24px;
border: 1px solid #F2EEDA;
}
.card img{
  width: 103px;
  height: 115.48px;
}
.card .bottom{
  display: flex;
  height: fit-content;
}
.card .bottom h3{
  font-weight: 400;
font-size: 20px;
line-height: 24px;
/* identical to box height */


color: #606060;
}
.bottom h3{
  color: #606060;
  margin-right: 32px;
}
.bottom .content div{
padding: 5px 16px;
background: #FFFFFF;
border: 1px solid rgba(0, 0, 0, 0.1);
box-sizing: border-box;
border-radius: 4px;
margin-top: 1px;
font-style: normal;
font-weight: 400;
font-size: 14px;
line-height: 20px;
color: #606060;
margin-bottom: 12px;
width: fit-content;
}
.card .bottom {
  position: relative;
}
.bottom .content span{
  position: absolute;
  bottom: -16px;
  right: 0;
}
.bottom .empty{
  padding: 5px 16px;
background: #FFFFFF;
box-sizing: border-box;
margin-top: 1px;
font-style: normal;
font-weight: 400;
font-size: 14px;
line-height: 20px;
color: #606060;
margin-bottom: 12px;
width: fit-content;
}